.cards-container {
  display: none;
}

.offcanvas-bottom {
  height: fit-content !important;
  max-height: 80% !important;
}

.drawer-handle {
  background: #dde2e5;
  border-radius: 2px;
  width: 65px;
  height: 4px;
}

.drawer-handle-wrapper {
  justify-content: center;
  width: 100%;
  padding: 4px 0 0;
  display: flex;
}

.details-offcanvas-body {
  flex-direction: column;
  gap: 12px;
  padding: 48px 24px 56px;
  display: flex;
}

.detail-title {
  color: var(--neutral-dark-dark-700, #212429);
  letter-spacing: .2px;
  font-family: Lato;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 24px;
}

.detail-status {
  color: #1c1e27;
  letter-spacing: .2px;
  font-family: Lato;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 16px;
}

.general-details-container {
  flex-direction: column;
  gap: 8px;
  display: flex;
}

.general-details {
  gap: 8px;
  display: flex;
}

.detail-label {
  color: var(--neutral-mid-mid-500, #6c747d);
  letter-spacing: .2px;
  width: 140px;
  font-family: Lato;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
}

.detail-value {
  text-overflow: ellipsis;
  color: var(--neutral-dark-black, #000);
  letter-spacing: .2px;
  font-family: Lato;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
  overflow: hidden;
}

th.each-cell {
  white-space: nowrap;
  min-width: fit-content;
  padding: 12px 30px 16px 24px !important;
}

th.sorting {
  white-space: nowrap;
  min-width: fit-content;
  padding: 12px 36px 16px 24px !important;
}

table.dataTable thead > tr > th.sorting:after, table.dataTable thead > tr > th.sorting:before {
  display: none !important;
}

.asc_desc_sort_wrapper {
  white-space: nowrap;
  flex-direction: column;
  gap: 0;
  display: inline-flex;
  position: relative;
  top: -4px;
  left: 6px;
}

.download-confirm-dialog-button {
  border: none;
}

@media (width <= 640px) {
  .dataTables_scroll {
    height: 100%;
  }

  .download-button {
    display: none !important;
  }

  .cards-container {
    flex-direction: column;
    gap: 12px;
    margin-bottom: 100px;
    padding: 16px;
    display: flex;
    overflow: auto;
  }

  .card {
    border: 1px solid var(--neutral-light-light-200, #dde2e5);
    background: #fff;
    border-radius: 6px;
    flex-direction: column;
    gap: 12px;
    padding: 16px 24px;
    display: flex;
    box-shadow: 0 2px 16px #00000014;
  }

  .card-title {
    color: #212429;
    letter-spacing: .2px;
    font-family: Lato;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 20px;
  }

  .card-info-container {
    flex-direction: column;
    gap: 8px;
    display: flex;
  }

  .card-info {
    gap: 8px;
    display: flex;
  }

  .card-info-label {
    color: #6c747d;
    letter-spacing: .2px;
    font-family: Lato;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 20px;
  }

  .card-info-value {
    color: #000;
    letter-spacing: .2px;
    font-family: Lato;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 20px;
  }

  .card-status, .detail-status {
    color: #fff;
    letter-spacing: .2px;
    background-color: #a3abb1;
    border-radius: 15px;
    width: fit-content;
    padding: 3px 8px;
    font-family: Lato;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 16px;
  }

  .dataTables_scrollHead, .dataTables_scrollBody {
    display: none !important;
  }

  .table-bottom {
    border-bottom: 0 solid #f0f3f4;
    width: 100%;
    position: fixed;
  }
}
/*# sourceMappingURL=detail.css.map */
